
.detailNav__root{position:relative;width:25px
}
.detailNav__item,.detailNav__item--active,.detailNav__item:hover,.detailNav__item--disable{position:absolute;height:130px;width:100%;overflow:hidden;cursor:pointer
}
.detailNav__item polygon,.detailNav__item--active polygon,.detailNav__item:hover polygon,.detailNav__item--disable polygon{fill:#00315a;stroke:#666;stroke-width:1;-webkit-transition:all .3s;transition:all .3s
}
.detailNav__item .detailNav__itemText,.detailNav__item--active .detailNav__itemText,.detailNav__item--disable .detailNav__itemText{position:absolute;width:100%;top:50%;left:0;line-height:16px;color:#fff;font-size:14px;text-align:center;-webkit-transform:translate(0, -50%);-ms-transform:translate(0, -50%);transform:translate(0, -50%)
}
.detailNav__item--active,.detailNav__item:hover{z-index:99 !important
}
.detailNav__item--active polygon,.detailNav__item:hover polygon{fill:#0393dc;stroke:#fff;stroke-width:1
}
.detailNav__item--disable{cursor:context-menu
}
.detailNav__item--disable polygon{fill:#939495
}



