
.warnStaticRiskControl__root{position:relative;padding-bottom:.75rem;background-color:#fff
}
.warnStaticRiskControl__root .detailsBasicTitile__root{color:#808080;background-color:transparent
}
.warnStaticRiskControl__item{position:relative;height:2.5rem;background-color:#f7f7f7;cursor:pointer;font-size:.77778rem;-webkit-transition:all .3s;transition:all .3s
}
.warnStaticRiskControl__item:not(:last-of-type){margin-bottom:1px
}
.warnStaticRiskControl__item--active .warnStaticRiskControl__arrow{transform:rotate(-180deg);-o-transform:rotate(-180deg);-ms-transform:rotate(-180deg);-moz-transform:rotate(-180deg);-webkit-transform:rotate(-180deg)
}
.warnStaticRiskControl__item--active,.warnStaticRiskControl__item:hover{background-color:#8895a8
}
.warnStaticRiskControl__item--active .warnStaticRiskControl__title,.warnStaticRiskControl__item:hover .warnStaticRiskControl__title{color:#fff
}
.warnStaticRiskControl__item--active .warnStaticRiskControl__text,.warnStaticRiskControl__item:hover .warnStaticRiskControl__text{color:#ffdb03
}
.warnStaticRiskControl__item--active .warnStaticRiskControl__arrow,.warnStaticRiskControl__item:hover .warnStaticRiskControl__arrow{color:#fff
}
.warnStaticRiskControl__title{position:relative;display:block;width:12rem;line-height:2.5rem;padding-left:1.375rem;float:left;color:#808080
}
.warnStaticRiskControl__title::after{position:absolute;content:"";right:0;top:.5625rem;width:.0625rem;height:1.375rem;background-color:#dcdbe0
}
.warnStaticRiskControl__info{float:left;margin-left:2.8125rem;height:2.5rem;width:1.25rem;background:url("../../../../assets/imgs/warnRiskControlInfo.png") no-repeat center;background-size:contain
}
.warnStaticRiskControl__text{position:relative;display:block;width:12.625rem;line-height:2.5rem;float:left;color:#00315a;text-align:center;font-size:1.33333rem
}
.warnStaticRiskControl__arrow{position:absolute;right:1.625rem;color:#8895a9;font-size:.75rem
}



