
.detailsHeader__root{position:relative;display:table;width:100%;height:3.75rem;padding-left:2.125rem;z-index:99
}
.detailsHeader__root::after{content:"";display:block;clear:both
}
.detailsHeader__root .searchBoxHasResList__root{position:relative;height:1.875rem;width:18.75rem;top:.9375rem;float:left
}
.detailsHeader__root .searchBox__input{-webkit-border-radius:1.25rem;border-radius:1.25rem
}
.detailsHeader__title{height:3.75rem;padding-right:.625rem;color:#333;font-weight:normal;font-size:1rem
}
.detailsHeader__title--risk{display:table-cell;width:15.4375rem;vertical-align:middle
}
.detailsHeader__title--details{float:left;line-height:3.75rem
}
.detailsHeader__analysis{position:relative;float:left;height:3.75rem;line-height:3.75rem;padding:0 .625rem 0 1.375rem;color:#e14340;font-weight:bold;font-size:1rem
}
.detailsHeader__analysis::before{position:absolute;left:0;content:'';width:1.1875rem;height:100%;background:url("../../../../assets/imgs/analysisIcon.png") no-repeat left center;background-size:contain
}
.detailsHeader__analysis a{text-decoration:none;color:#e14340
}



