
.selectNew__root{width:7.5rem;display:inline-block;position:relative;cursor:pointer;height:28px;line-height:28px;padding:0 .8125rem 1.875rem;font-size:14px;-webkit-border-radius:2px;border-radius:2px;border:1px solid #e4eaec;background:#fff;vertical-align:middle;z-index:10
}
