
@import url(../../../../assets/css/font/iconfont.css);
.warnDynamicRisk__picBox{width:62.5rem;height:830px;float:left;background:#666;position:relative;overflow:hidden
}
.warnDynamicRisk__Pic{float:left;width:50%;height:800px;padding:0px 10px 20px 10px;position:relative;overflow:hidden;background:#fff
}
.warnDynamicRisk__toolBoxWrap{width:100%;height:100px;background:#fff;position:relative;z-index:99;padding-top:1.875rem
}
.warnDynamicRisk__toolBox{width:21.25rem;margin:0 auto;font-size:18px
}
.warnDynamicRisk__toolBox .select__root{top:0px;width:5rem;margin-left:10px
}
.warnDynamicRisk__scaleBox{width:6.625rem;height:1.625rem;display:inline-block;background:#e0e1e9;font-size:1rem;line-height:1.625rem;text-align:center;margin-left:.0625rem;vertical-align:middle
}
.warnDynamicRisk__scaleBox .reduce,.warnDynamicRisk__scaleBox .add{height:1.625rem;width:1.625rem;background:#ebebf3;text-align:center;line-height:22px;cursor:pointer;font-size:1.4rem;line-height:1.4rem
}
.warnDynamicRisk__scaleBox .reduce{float:left;color:#23a0e0;font-weight:bold
}
.warnDynamicRisk__scaleBox .add{float:right;color:#23a0e0;font-weight:bold
}
.warnDynamicRisk__back{width:1.625rem;height:1.625rem;display:inline-block;vertical-align:middle;margin-left:10px
}
.warnDynamicRisk__back::before{font-size:1.5625rem;cursor:pointer;color:#23a0e0
}
.warnDynamicRisk__img{position:relative;display:block;margin:2.8125rem auto 0;transform-origin:left top;-o-transform-origin:left top;-ms-transform-origin:left top;-moz-transform-origin:left top;-webkit-transform-origin:left top
}



