
.details__riskBtn{position:relative;height:1.625rem;padding:0 1.75rem 0 .875rem;top:1.0625rem;margin-left:2.125rem;-webkit-border-radius:.125rem;border-radius:.125rem;color:#fff;border:none;cursor:pointer;font-size:.66667rem;background-color:#fb9f00
}
.details__riskBtn--up::after,.details__riskBtn--down::after{position:absolute;top:50%;right:.875rem;margin-top:-7px;content:'\e600';font-family:'iconfont'
}
