
.login__root{position:fixed;top:0;left:0;right:0;bottom:0;background:url("../../assets/imgs/loginBg.png");background-size:cover
}
.login__wrap{position:absolute;height:23.625rem;padding-left:50.8125rem;top:50%;transform:translateY(-50%);-o-transform:translateY(-50%);-ms-transform:translateY(-50%);-moz-transform:translateY(-50%);-webkit-transform:translateY(-50%);background:url("../../assets/imgs/mainLogo.png") no-repeat 24.625rem center
}
.login__box{position:relative;width:21.25rem;height:15.625rem;border:1px solid #0393dc;padding-top:2.8125rem
}
.login__box::before{content:"";position:absolute;width:1px;height:4.375rem;left:-1.25rem;top:4px;background-color:#21A0E1
}
.login__box::after{content:"";position:absolute;width:1px;height:4.375rem;right:-1.25rem;bottom:0;background-color:#21A0E1
}
.login__username,.login__pwd{height:3.25rem;line-height:3.25rem;text-align:center
}
.login__username input,.login__pwd input{width:15.3125rem;height:2rem;padding:0 .5rem;border:1px solid #aaa;background-color:#fff;-webkit-transition:all .3s;transition:all .3s
}
.login__username input:focus,.login__pwd input:focus{border:1px solid #0393dc;-webkit-box-shadow:0 0 3px #0393dc;box-shadow:0 0 3px #0393dc
}
.login__err{height:2.25rem;line-height:2.25rem;padding-left:2.875rem;color:#be0000;text-align:left;font-size:.77778rem
}
.login__btn{position:absolute;bottom:0;width:100%;height:3.125rem;color:#fff;font-size:.88889rem;background-color:#30a5e1;border:none;cursor:pointer
}
.login__line1{position:absolute;left:0;bottom:-1.125rem;width:42.5rem;height:3.125rem
}
.login__line2{position:absolute;left:-20.25rem;top:0;width:19rem;height:.5rem
}



