.loader__root{position:fixed;left:0;right:0;top:0;bottom:0;z-index:200;background-color:rgba(0,0,0,0.3)
}
.loader__root::after{content:"";position:absolute;left:50%;top:50%;width:25px;height:25px;-webkit-border-radius:50%;border-radius:50%;-webkit-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);transform:translate(-50%, -50%);animation:load 1.1s infinite ease;-ms-animation:load 1.1s infinite ease;-moz-animation:load 1.1s infinite ease;-webkit-animation:load 1.1s infinite ease
}
.loader__error{position:absolute;width:294px;height:168px;left:50%;top:50%;padding-top:20px;transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%);z-index:2;font-size:14px;text-align:center;background-color:#fff;-webkit-box-shadow:0 0 4px 4px rgba(0,0,0,0.3);box-shadow:0 0 4px 4px rgba(0,0,0,0.3)
}
.loader__smileIcon{display:block;width:70px;height:70px;margin:0 auto
}
.loader__msg{padding:0 40px;margin-top:20px
}
.loader__close{width:18px;height:18px;position:absolute;top:9px;right:9px;cursor:pointer;background:url("../../assets/imgs/loader-close.png") center center no-repeat
}



