
@import url(../../../../assets/css/font/iconfont.css);
.warnDynamicRisk__compareBox{width:24.375rem;height:830px;float:right;background:#fff
}
.warnDynamicRisk__compareBoxInset{width:100%;padding:10px 15px 10px 15px
}
.warnDynamicRisk__contentTitle{width:100%;background:#8895a9;height:38px;line-height:38px
}
.warnDynamicRisk__contentTitle p{display:inline-block
}
.warnDynamicRisk__contentTitle .contentTitleWrap{width:200px;margin:0 auto
}
.warnDynamicRisk__contentTitle .contentTitleWrap p{width:80px;height:24px;line-height:24px;text-align:center;background:#fff;color:#666;font-size:14px;vertical-align:middle;-webkit-border-radius:4px;border-radius:4px
}
.warnDynamicRisk__loading{padding:10px 10px;font-size:14px;color:#000;text-align:center
}
.warnDynamicRisk__item{overflow:hidden;height:50px;position:relative;-webkit-transition:all 0.3s ease;transition:all 0.3s ease
}
.warnDynamicRisk__item .warnDynamicRisk__title{height:40px;line-height:40px;background:#e9ecf3;-webkit-border-radius:5px;border-radius:5px;padding-left:20px;border-radius:5px
}
.warnDynamicRisk__item .warnDynamicRisk__yellow{color:#ff9b00;font-weight:normal
}
.warnDynamicRisk__item .warnDynamicRisk__blue{color:#00315a
}
.warnDynamicRisk__item .warnDynamicRisk__itemTitle{height:40px;line-height:40px;width:100%;background:#e5f0f9;-webkit-border-radius:5px;border-radius:5px;margin:5px 0  5px 0;padding-left:20px;font-size:16px;cursor:pointer
}
.warnDynamicRisk__item .itemTitleNum{float:right;width:180px;cursor:pointer
}
.warnDynamicRisk__item .itemTitleNum span{width:55px;display:inline-block;text-align:left;font-weight:bold
}
.warnDynamicRisk__item .itemTitleNum .icon-xiaojiantoushang:before{position:relative;top:-2px
}
.warnDynamicRisk__item .itemTitleNum .symbol{color:#999;font-size:12px
}
.warnDynamicRisk__item .itemContent{width:100%;height:226px
}
.warnDynamicRisk__item .itemContentLeft{height:100%;width:25%;padding-right:5px;float:left
}
.warnDynamicRisk__item .itemContentLeft-inset{background:#e9ecf3;width:100%;height:100%;-webkit-border-radius:5px;border-radius:5px;padding-top:12px
}
.warnDynamicRisk__item .textWrap{width:30px;height:100px;margin:0 auto;position:relative;left:10px;font-size:16px;line-height:16px
}
.warnDynamicRisk__item .itemContentRight{height:100%;width:75%;float:left;position:relative;-webkit-border-radius:5px;border-radius:5px;padding-left:3px
}
.warnDynamicRisk__item .itemContentRight .title{padding-left:28px;font-size:.77778rem
}
.warnDynamicRisk__item .itemContentRight .title span{padding-right:10px;display:inline-block
}
.warnDynamicRisk__item .itemContentRight .title span:nth-child(1){width:5.625rem
}
.warnDynamicRisk__item .itemContentRight .title span:nth-child(2){width:2.8125rem
}
.warnDynamicRisk__item .itemContentRight .title span:nth-child(3){width:2.8125rem
}
.warnDynamicRisk__item .itemContentRight .scroll{height:70px;background:#e9ecf3;overflow-x:hidden;overflow-y:auto;-webkit-border-radius:5px;border-radius:5px;margin-top:2px;margin-bottom:2px
}
.warnDynamicRisk__item .itemContentRight .scroll li{color:#808080;font-size:14px;padding:0px 0px;text-align:center;height:24px;line-height:24px;cursor:pointer;overflow:hidden
}
.warnDynamicRisk__item .itemContentRight .scroll li:hover{background:#fff
}



