
@import url(../../assets/css/font/iconfont.css);
.main__root{position:absolute;top:0;bottom:0;left:0;right:0;background:#e7f4fa url("../../assets/imgs/mainBg.jpg") no-repeat center;background-size:100% 100%
}
.main__logo{position:absolute;width:160px;height:auto;top:15%;left:50%;margin-left:-66px
}
.main__list{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;position:absolute;bottom:15%;left:50%;transform:translateX(-50%);-o-transform:translateX(-50%);-ms-transform:translateX(-50%);-moz-transform:translateX(-50%);-webkit-transform:translateX(-50%)
}
.main__icon{position:relative;display:block;height:90px
}
.main__icon polygon{fill:#00315a;-webkit-transition:all .3s;transition:all .3s
}
.main__iconI{position:absolute;left:50%;top:50%;color:#ccc;font-size:46px;-webkit-transition:all .3s;transition:all .3s;transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%)
}
.main__item{width:256px;height:124px;cursor:pointer
}
.main__item--active polygon,.main__item:hover polygon{fill:#0393dc
}
.main__item--active .main__iconI,.main__item:hover .main__iconI{color:#fcee21
}
.main__item--active .main__title,.main__item:hover .main__title{font-weight:bold
}
.main__title{height:34px;line-height:34px;color:#4d4d4d;text-align:center;font-size:.77778rem;font-weight:normal
}



