
.accountLogout__root{height:3.75rem;line-height:3.75rem;float:right
}
.accountLogout__root .iconfont{margin-right:.375rem;font-size:.77778rem
}
.accountLogout__user{position:relative;margin-right:1.875rem;color:#fff;font-size:.77778rem;cursor:pointer
}
.accountLogout__logout{color:#e6a106;cursor:pointer;font-size:.77778rem
}
.accountLogout_drop{position:absolute;width:88px;top:2.3125rem;left:0;z-index:101
}
.accountLogout_dropItem{height:30px;line-height:30px;cursor:pointer;text-align:center;background-color:#00315A;-webkit-transition:all .3s;transition:all .3s
}
.accountLogout_dropItem:hover{background-color:#325B7B
}



