
.page__root{overflow:hidden;color:#666
}
.page__pageList{float:left;overflow:hidden
}
.page__pagesItem{min-width:2.25rem;height:1.75rem;line-height:1.75rem;margin-right:.625rem;padding:0 .25rem;float:left;cursor:pointer;text-align:center;font-size:.66667rem;border:1px solid #D1D1D1;-webkit-border-radius:.125rem;border-radius:.125rem;-webkit-transition:all .3s;transition:all .3s
}
.page__pagesItem--active,.page__pagesItem:hover{color:#fff;border-color:#0393dc;background-color:#0393dc
}
.page__count{height:1.875rem;line-height:1.875rem;margin-right:.5rem;float:left;font-size:.66667rem
}
.page__to{height:1.875rem;float:left;overflow:hidden;font-size:.66667rem
}
.page__toInput{height:1.6875rem;width:2.875rem;margin:0 .25rem;padding:0 .25rem;border:none;color:#666;text-align:center;-webkit-border-radius:.125rem;border-radius:.125rem;background-color:#fff
}
.page__toBtn{height:1.6875rem;width:2.25rem;color:#666;cursor:pointer;font-size:.66667rem;border:1px solid #D1D1D1;-webkit-border-radius:.125rem;border-radius:.125rem;background-color:transparent
}



