
.nav__root{height:2.5rem;line-height:2.5rem;padding:0 1.25rem;background-color:#fff
}
.nav__item{position:relative;padding:0 .875rem;float:left;color:#4c4c4c;cursor:pointer;font-size:.77778rem
}
.nav__item:not(:last-of-type){margin-right:4.25rem
}
.nav__item--active{color:#00315a;font-weight:bold
}
.nav__item--active::after{content:"";position:absolute;width:100%;height:2px;left:0;bottom:0;background-color:#00315a
}



